/* Основной контейнер */
.main-menu {
  background-color: #1e1e1e; /* Очень тёмный фон */
  color: #ffffff; /* Белый текст */
  padding: 20px;
  min-height: 100vh; /* Высота экрана */
  font-family: 'Arial', sans-serif;
}

/* Заголовок */
.main-menu-title {
  font-size: 2.5rem;
  color: #ff6f00; /* Оранжевый акцент */
  margin-bottom: 20px;
  text-align: center;
}

/* Контейнер карточек */
.menu-container {
  display: flex; /* Горизонтальное расположение */
  flex-wrap: nowrap; /* Без переноса */
  gap: 20px;
  justify-content: center;
  align-items: center;
  overflow-x: auto; /* Прокрутка для маленьких экранов */
  padding: 10px 0;
}

/* Карточки */
.menu-card {
  background-color: #2a2a2a; /* Тёмно-серый фон карточек */
  border-radius: 10px;
  text-align: center;
  padding: 20px;
  min-width: 250px; /* Увеличенная ширина карточки */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.menu-card:hover {
  background-color: #ff6f00; /* Оранжевый фон при наведении */
  transform: scale(1.05); /* Увеличение карточки */
}

/* Изображения */
.card-image {
  width: 180%; /* Ещё больше изображений */
  height: auto;
  border-radius: 10px;
  margin-bottom: 15px;
}

/* Кнопки */
.card-button {
  padding: 10px 15px; /* Увеличенные кнопки */
  background-color: rgba(255, 111, 0, 0.8); /* Полупрозрачный оранжевый фон */
  color: #ffffff; /* Белый текст */
  font-size: 1.1rem; /* Чуть больший текст кнопок */
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.card-button:hover {
  background-color: rgba(255, 111, 0, 1); /* Насыщенный оранжевый при наведении */
  transform: scale(1.1); /* Лёгкое увеличение кнопки */
}

/* Адаптивность */
@media (max-width: 600px) {
  .menu-container {
    flex-wrap: wrap; /* Перенос карточек на маленьких экранах */
    justify-content: center;
  }

  .card-button {
    padding: 8px 12px; /* Немного уменьшить кнопки для мобильных */
  }
}
